import goodsHeader from "./goodsHeader.vue";
import interfaces from '../../utils/interfaces.js';
import popupSpec from '../../components/popupSpec.vue'
export default{
	data(){
		return{
			isKeep:false,//收藏开关
			currentSwiper:0,//轮播图下标
			goodsData:{//包含轮播图 规格 评论 详情
				swiperList:[],
				spec:[],
				comment:[
					{face:"",content:"",username:""}
				],
				descriptionStr:"",
			},
			goodsInfo:{//包含名字 价格 数量 规格
				name:"",
				price:"",
				number:1,
				spec:""
			},
			spaceInfo: {
				showSpace: false
			}
		}
	},
	components:{
		goodsHeader,
		popupSpec
	},
	onLoad(option){
		// console.log(option);
		this.goodsInfo = JSON.parse(option.goodsInfo)
		this.initDate()
	},
	methods:{
		setSelectSpec(item){//popupSpec组件的注册事件
			// console.log(item);获取到子级传递过来的内容
			this.goodsInfo.spec=item;
		},
		hideSpec(){////popupSpec组件的注册事件
			this.spaceInfo.showSpace=false;
		},
		sub(){
			//如果goodsInfo的的number值小于等于1 那就跳出函数
			if(this.goodsInfo.number <= 1){
				return;
			}
			this.goodsInfo.number--;
		},
		add(){
			this.goodsInfo.number++;
		},
		initDate(){
			this.request({
				url:interfaces.getGoods,
				success:((res)=>{
					// console.log(res.data);
					this.goodsData=res.data
				})
			})
		},
		swiperChange(event){
			this.currentSwiper=event.detail.current;
		},
		//查看所有评价
		handleRatings(){
			// 本地存储
			//uni的本地同步存储uni.setStorageSync(key名，value值)
			uni.setStorageSync('comments',this.goodsData.comment);
			uni.navigateTo({
				url:'./ratings'
			})
		},
		keep(){
			this.isKeep=!this.isKeep;
			if(this.isKeep){
				uni.showToast({
					title:"已收藏",
					icon:"success"
				})
			}else{
				uni.showToast({
					title:"取消收藏",
					icon:"success"
				})
			}
		},
		joinCart(){//加入购物车
			//存储到本地
			//现在本地存储去取
			uni.getStorage({
				key:"goodsList",
				success:(res=>{
					// console.log(res.data);
					// 如果是拿到的数据
					let goodsList=res.data;
					
					//定义一个开关 查找商品是够存在
					let isExist=false;
					
					//这里的遍历的作用是用来判断 你点击购物车的商品是不是同一个东西 如果是同一个东西那就数量累加 如果是同一个商品的id但是不同的规格 那就再次存储
					goodsList.forEach(goods =>{
						if(goods.goods_id==this.goodsInfo.goods_id && goods.spec==this.goodsInfo.spec){
							goods.number+=Number(this.goodsInfo.number);
							isExist=true;
						}
						
					})
					if(!isExist){
						goodsList.push(this.goodsInfo);
						//跟新本地存储
						this.setGoodsList(goodsList);
					}
					
				}),
				fail:(err=>{//第一次加入购物车 没有得到数据 所以fail就存储
				
					let goodsList=[];
					//将goodInfo 信息放到定义的空数组里 准备存入本地存储
					goodsList.push(this.goodsInfo);
					
					//本地存储中存储数据
					this.setGoodsList(goodsList);
				})
			})
		},
		setGoodsList(goodsList){
			//往本地存储 存储数据
			uni.setStorage({
				key:"goodsList",
				data:goodsList,
				success:function(){
					uni.showToast({
						icon:"success",
						title:"添加成功"
					})
				}
			})
		},
		handleBuy(){//立即购买
			//这里存储的是单个的goodsinfo 是个对象 所以 我们要先准备一个空数组
			let tempList=[];
			tempList.push(this.goodsInfo);
			uni.setStorage({
				key:"confirmList",
				data:tempList,
				success:()=>{
					uni.navigateTo({
						url:"../order/cofirm"
					})
				}
			})
		}
	}
}
